<template>
  <div class="cart-page">
    <div class="cart-header">
      <h1>Shopping Cart</h1>
      <p v-if="cart.state.items.length" class="items-count">
        {{ cart.state.items.length }} {{ cart.state.items.length === 1 ? 'item' : 'items' }}
      </p>
    </div>

    <div class="cart-content" v-if="cart.state.items.length">
      <div class="cart-items">
        <TransitionGroup name="list">
          <div v-for="item in cart.state.items" 
               :key="item.id" 
               class="cart-item">
            <div class="item-info">
              <div class="item-image-container">
                <img v-if="getCardImage(item)" 
                     :src="getCardImage(item)" 
                     :alt="item.name"
                     class="card-image"
                     :class="{ 'product-image': item.type !== 'single' }">
                <div v-else class="image-placeholder">
                  <span class="material-symbols-rounded">hourglass_empty</span>
                </div>
              </div>
              <div class="item-details">
                <h3>{{ item.name }}</h3>
                <div v-if="item.type === 'single'" class="item-meta">
                  <div class="set-info" v-if="cardData[item.name]">
                    <img v-if="getSetSymbol(item.name)" 
                         :src="getSetSymbol(item.name)" 
                         :alt="getSetName(item.name)"
                         class="set-symbol">
                    <span class="set-name">{{ getSetName(item.name) }}</span>
                    <span class="set-code">{{ getSetSeries(item.name) }}</span>
                  </div>
                  <div class="card-number" v-if="cardData[item.name]">
                    {{ getCardNumber(item.name) }}
                  </div>
                  <span v-if="getRarity(item.name)" 
                        :class="['rarity-badge', getRarity(item.name).toLowerCase()]">
                    {{ getRarity(item.name) }}
                  </span>
                </div>
                <div v-else class="item-meta">
                  <span class="product-type">{{ item.type }}</span>
                  <span v-if="item.condition" class="condition-badge">
                    {{ item.condition }}
                  </span>
                </div>
                <div class="price-info">
                  <span class="price">€{{ formatPrice(item.price) }}</span>
                  <span class="market-price" v-if="getMarketPrice(item.name)">
                    Market: €{{ formatPrice(getMarketPrice(item.name)) }}
                  </span>
                </div>
              </div>
            </div>
            
            <div class="item-actions">
              <div class="quantity-controls">
                <button @click="updateQuantity(item.id, item.quantity - 1)" 
                        :disabled="item.quantity <= 1"
                        class="quantity-btn">
                  <span class="material-symbols-rounded">remove</span>
                </button>
                <span class="quantity">{{ item.quantity }}</span>
                <button @click="updateQuantity(item.id, item.quantity + 1)"
                        class="quantity-btn">
                  <span class="material-symbols-rounded">add</span>
                </button>
              </div>
              <div class="subtotal">
                <span class="subtotal-label">Subtotal</span>
                <span class="subtotal-value">€{{ formatPrice(item.price * item.quantity) }}</span>
              </div>
              <button class="remove-button" @click="removeItem(item.id)">
                <span class="material-symbols-rounded">delete</span>
              </button>
            </div>
          </div>
        </TransitionGroup>
      </div>

      <div class="cart-summary">
        <h2>Order Summary</h2>
        <div class="summary-details">
          <div class="summary-row">
            <span>Subtotal</span>
            <span>€{{ formatPrice(cart.state.total) }}</span>
          </div>
          <div class="summary-row">
            <span>Shipping</span>
            <span>Free</span>
          </div>
          <div class="summary-total">
            <span>Total</span>
            <span>€{{ formatPrice(cart.state.total || 0) }}</span>
          </div>
        </div>
        <button 
          @click="checkout"
          class="checkout-button"
        >
          <span class="material-symbols-rounded">shopping_bag</span>
          Checkout
        </button>
      </div>
    </div>

    <div v-else class="empty-cart">
      <span class="material-symbols-rounded">shopping_cart</span>
      <h2>Your cart is empty</h2>
      <p>Looks like you haven't added anything to your cart yet</p>
      <router-link to="/store" class="continue-shopping">
        Continue Shopping
      </router-link>
    </div>
  </div>
</template>

<script>
import { defineComponent, ref, onMounted, watch } from 'vue'
import { useRouter } from 'vue-router'
import cart from '@/store/cart'
import { getProxiedImageUrl, handleImageError } from '@/utils/imageProxy'
import pokemonApi from '@/services/pokemonApi'

export default defineComponent({
  name: 'Cart',
  setup() {
    const router = useRouter()
    const cardImages = ref({})
    const cardData = ref({})

    const fetchCardData = async (cartItem) => {
      if (cartItem.type !== 'single') {
        const metadata = cartItem.metadata || {}
        cardData.value[cartItem.name] = {
          ...cartItem,
          images: { small: cartItem.image_url },
          set: metadata.set || {},
          rarity: metadata.rarity || null
        }
        cardImages.value[cartItem.name] = cartItem.image_url
        return
      }

      if (!cardData.value[cartItem.name]) {
        try {
          let data;
          
          // Try to fetch by tcg_id first
          if (cartItem.tcg_id) {
            console.log('Fetching by tcg_id:', cartItem.tcg_id);
            const response = await pokemonApi.getCard(cartItem.tcg_id);
            if (response.data) {
              data = response.data;
            }
          }
          
          // If no data found by tcg_id, try searching by name and set
          if (!data) {
            console.log('Falling back to name search');
            const response = await pokemonApi.getCards({
              q: `name:"${cartItem.name}"`,
              pageSize: 1
            });
            data = response.data[0];
          }
          
          if (data) {
            console.log('Found card data:', data);
            cardData.value[cartItem.name] = data;
            cardImages.value[cartItem.name] = data.images.small;
          }
        } catch (error) {
          console.error('Error fetching card:', error);
          // Fallback to stored metadata
          const metadata = cartItem.metadata || {}
          cardData.value[cartItem.name] = {
            ...cartItem,
            images: { small: cartItem.image_url },
            set: metadata.set || {},
            rarity: metadata.rarity || null
          }
          cardImages.value[cartItem.name] = cartItem.image_url;
        }
      }
    }

    // Fetch data for all cards in cart on mount
    onMounted(async () => {
      await Promise.all(cart.state.items.map(fetchCardData))
    })

    // Watch for cart changes to fetch new images
    watch(() => cart.state.items, async (newItems) => {
      const newCartItems = newItems.filter(item => !cardImages.value[item.name])
      if (newCartItems.length > 0) {
        await Promise.all(newCartItems.map(fetchCardData))
      }
    }, { deep: true })

    function getImageUrl(item) {
      return getProxiedImageUrl(item.image_url)
    }

    function updateQuantity(itemId, quantity) {
      // Ensure quantity is a valid number
      const newQuantity = parseInt(quantity)
      if (newQuantity > 0) {
        cart.updateQuantity(itemId, newQuantity)
      }
    }

    function removeItem(itemId) {
      cart.removeItem(itemId)
    }

    function checkout() {
      router.push('/checkout')
    }

    const formatPrice = (price) => {
      // Ensure we're working with numbers and handle invalid inputs
      const number = parseFloat(price)
      return isNaN(number) ? '0.00' : number.toFixed(2)
    }

    // Helper methods to safely access card data
    const getSetSymbol = (cardName) => {
      const card = cardData.value[cardName]
      if (!card || !card.set || !card.set.images) return null
      return card.type === 'single' ? card.set.images.symbol : null
    }

    const getSetName = (cardName) => {
      const card = cardData.value[cardName]
      if (!card || !card.set) return 'Unknown Set'
      return card.set.name
    }

    const getSetSeries = (cardName) => {
      const card = cardData.value[cardName]
      if (!card || !card.set) return null
      return card.set.series
    }

    const getCardNumber = (cardName) => {
      const card = cardData.value[cardName];
      if (!card) return '';
      
      const metadata = card.metadata || {};
      const number = card.number || (metadata ? metadata.number : null);
      const set = card.set || {};
      const metaSet = metadata.set || {};
      const total = set.printedTotal || (metaSet ? metaSet.printedTotal : null);
      
      if (!number || !total) return '';
      return `${number}/${total}`;
    }

    const getRarity = (cardName) => {
      const card = cardData.value[cardName]
      if (!card) return null
      return card.rarity
    }

    const getMarketPrice = (cardName) => {
      const card = cardData.value[cardName]
      if (!card || !card.tcgplayer || !card.tcgplayer.prices || !card.tcgplayer.prices.holofoil) {
        return null
      }
      return card.tcgplayer.prices.holofoil.market
    }

    // Add helper method to get card image
    const getCardImage = (item) => {
      if (item.type !== 'single') {
        return item.image_url;
      }

      // Try multiple image sources in order of preference
      if (item.image_url) {
        return item.image_url; // Original image URL
      }
      
      if (cardImages.value[item.name]) {
        return cardImages.value[item.name]; // Cached image from API
      }
      
      if (item.tcg_id) {
        const [setId, cardNumber] = item.tcg_id.split('-');
        return `https://images.pokemontcg.io/${setId}/${cardNumber}_hires.png`;
      }
      
      if (item.metadata && item.metadata.set && item.metadata.set.id && item.metadata.number) {
        return `https://images.pokemontcg.io/${item.metadata.set.id}/${item.metadata.number}_hires.png`;
      }

      return null; // No image found
    };

    return {
      cart,
      cardImages,
      cardData,
      getImageUrl,
      handleImageError,
      updateQuantity,
      removeItem,
      checkout,
      formatPrice,
      getSetSymbol,
      getSetName,
      getSetSeries,
      getCardNumber,
      getRarity,
      getMarketPrice,
      getCardImage,
    }
  }
})
</script>

<style scoped>
.cart-page {
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem;
  min-height: calc(100vh - 64px);
  background: var(--background-primary);
}

.cart-header h1 {
  font-size: 40px;
  font-weight: 700;
  letter-spacing: -0.025em;
  background: linear-gradient(135deg, #ffffff 0%, #a7a7a7 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  margin-bottom: 8px;
}

.items-count {
  color: var(--text-secondary);
  font-size: 16px;
  margin-top: 0;
}

.cart-content {
  display: grid;
  grid-template-columns: 1fr 320px;
  gap: 32px;
  margin-top: 32px;
}

.cart-items {
  background: rgba(255, 255, 255, 0.05);
  border-radius: 20px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  overflow: hidden;
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
}

.cart-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 24px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  background: rgba(255, 255, 255, 0.02);
  transition: all 0.3s ease;
}

.cart-item:hover {
  background: rgba(255, 255, 255, 0.05);
}

.item-image-container {
  width: 100px;
  height: 140px;
  border-radius: 12px;
  overflow: hidden;
  background: var(--background-elevated);
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: var(--shadow-md);
  transition: all 0.3s ease;
}

.item-image-container:hover {
  transform: scale(1.05);
  box-shadow: var(--shadow-lg);
}

.card-image {
  width: 100%;
  height: 100%;
  object-fit: contain;
  transition: transform 0.3s ease;
}

.item-info {
  display: flex;
  gap: 20px;
}

.item-details h3 {
  margin: 0;
  font-size: 16px;
  font-weight: 500;
}

.item-meta {
  margin-top: 8px;
  display: flex;
  gap: 8px;
}

.set-info {
  display: flex;
  align-items: center;
  gap: 8px;
  background: var(--surface-background);
  padding: 4px 12px;
  border-radius: 100px;
}

.set-symbol {
  width: 20px;
  height: 20px;
  object-fit: contain;
}

.set-name {
  font-size: 13px;
  color: var(--text-secondary);
}

.set-code {
  font-size: 12px;
  color: var(--text-tertiary);
  font-weight: 500;
}

.card-number {
  font-size: 13px;
  color: var(--text-tertiary);
  font-family: monospace;
}

.rarity-badge {
  padding: 4px 12px;
  border-radius: 100px;
  font-size: 12px;
  font-weight: 600;
}

.rarity-badge.common { background: var(--surface-background); color: var(--text-secondary); }
.rarity-badge.uncommon { background: var(--system-green); color: white; }
.rarity-badge.rare { background: var(--system-blue); color: white; }
.rarity-badge.amazing { background: var(--system-purple); color: white; }
.rarity-badge.ultra { background: var(--system-orange); color: white; }
.rarity-badge.secret { background: var(--system-pink); color: white; }

.price-info {
  margin-top: 8px;
  display: flex;
  align-items: baseline;
  gap: 12px;
}

.price {
  font-size: 18px;
  font-weight: 600;
  color: var(--accent-primary);
}

.market-price {
  font-size: 13px;
  color: var (--text-tertiary);
}

.quantity-controls {
  background: var(--surface-background);
  border-radius: 12px;
  padding: 4px;
  display: inline-flex;
  align-items: center;
  gap: 12px;
}

.quantity {
  min-width: 24px;
  text-align: center;
  font-weight: 600;
}

.quantity-btn {
  width: 32px;
  height: 32px;
  border: none;
  border-radius: 8px;
  background: var(--background-elevated);
  color: var(--text-primary);
  cursor: pointer;
  transition: all 0.2s ease;
  display: flex;
  align-items: center;
  justify-content: center;
}

.quantity-btn:hover:not(:disabled) {
  background: var(--accent-primary);
  color: white;
  transform: translateY(-1px);
}

.quantity-btn:disabled {
  color: var(--text-tertiary);
  cursor: not-allowed;
}

.cart-summary {
  background: rgba(255, 255, 255, 0.05);
  border-radius: 20px;
  padding: 24px;
  position: sticky;
  top: 100px;
  height: fit-content;
  border: 1px solid rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
}

.cart-summary h2 {
  font-size: 24px;
  font-weight: 600;
  margin-bottom: 24px;
  background: linear-gradient(135deg, #ffffff 0%, #a7a7a7 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.summary-row {
  display: flex;
  justify-content: space-between;
  margin-bottom: 12px;
  color: var(--text-secondary);
  font-size: 15px;
}

.summary-total {
  display: flex;
  justify-content: space-between;
  margin-top: 16px;
  padding-top: 16px;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
  font-size: 18px;
  font-weight: 600;
}

.checkout-button {
  width: 100%;
  padding: 16px;
  background: var(--accent-primary);
  color: white;
  border: none;
  border-radius: 12px;
  font-size: 16px;
  font-weight: 600;
  margin-top: 24px;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  background: linear-gradient(135deg, var(--accent-primary), var(--system-purple));
}

.checkout-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 16px rgba(var(--accent-primary-rgb), 0.3);
}

.empty-cart {
  text-align: center;
  padding: 64px 24px;
  color: var(--text-secondary);
}

.empty-cart h2 {
  font-size: 24px;
  margin: 16px 0;
  background: linear-gradient(135deg, #ffffff 0%, #a7a7a7 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.continue-shopping {
  display: inline-block;
  margin-top: 24px;
  padding: 16px 32px;
  background: var(--accent-primary);
  color: white;
  text-decoration: none;
  border-radius: 12px;
  font-weight: 600;
  transition: all 0.3s ease;
  background: linear-gradient(135deg, var(--accent-primary), var(--system-purple));
}

.continue-shopping:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 16px rgba(var(--accent-primary-rgb), 0.3);
}

/* List transition animations */
.list-enter-active,
.list-leave-active {
  transition: all 0.3s ease;
}

.list-enter-from,
.list-leave-to {
  opacity: 0;
  transform: translateX(-30px);
}

@media (max-width: 768px) {
  .cart-content {
    grid-template-columns: 1fr;
  }
  
  .cart-summary {
    position: static;
  }
}

.image-placeholder {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: var(--background-elevated);
  color: var(--text-tertiary);
}

.image-placeholder .material-symbols-rounded {
  font-size: 24px;
  animation: pulse 1.5s infinite;
}

@keyframes pulse {
  0%, 100% { opacity: 0.5; }
  50% { opacity: 1; }
}

.remove-button {
  width: 36px;
  height: 36px;
  border: none;
  border-radius: 8px;
  background: var(--surface-background);
  color: var(--text-tertiary);
  cursor: pointer;
  transition: all 0.2s ease;
  display: flex;
  align-items: center;
  justify-content: center;
}

.remove-button:hover {
  background: var(--system-red);
  color: white;
  transform: translateY(-1px);
}

.subtotal {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.subtotal-label {
  font-size: 13px;
  color: var(--text-tertiary);
}

.subtotal-value {
  font-size: 16px;
  font-weight: 600;
  color: var(--text-primary);
}

.product-image {
  object-fit: cover !important;
}

.product-type {
  text-transform: capitalize;
  padding: 4px 12px;
  border-radius: 100px;
  font-size: 12px;
  background: var(--surface-background);
  color: var(--text-secondary);
}

.condition-badge {
  padding: 4px 12px;
  border-radius: 100px;
  font-size: 12px;
  background: var(--accent-primary);
  color: white;
}
</style>
